import axios from 'axios';

let host = ''
if (process.env.NODE_ENV === 'production') {
     host = 'http://api.agentily.com/';
  } else {
     host = 'http://127.0.0.1:8000/';
  }
  

// 用户登录
export const APIlogin = p => { return axios.post(`${host}login/`,p)}
// 获取用户信息-需要权限
export const APIuser = username => { return axios.get(`${host}users/${username}/`) }
// 更新用户信息
export const APIupdateuserinfo = (userinfo) => { return axios.patch(`${host}users/1/`,userinfo) }

// 获取用户列表
export const APIUlist = p => { return axios.get(`${host}u/`,p) }
// 获取用户详情-公开
export const APIU = id => { return axios.get(`${host}u/${id}/`)}

// 七牛操作
// 删除图片
export const APIQiniuDelete = p => { return axios.post(`${host}qiniu-delete/`,p)}

// 上传房源
export const APIuploadroom = p => { return axios.post(`${host}rooms/`,p) }
// 获取房源列表
export const APIGetroomlist = p => { return axios.get(`${host}rooms/`,p) }
// 获取房源详情，参数就是过滤
export const APIGetroom = id => { return axios.get(`${host}rooms/${id}/`) }


// 发起家庭
export const APIstartfamily = p => {return axios.post(`${host}family/`,p)}
// 获取家庭列表
export const APIgetfamily = p => {return axios.get(`${host}family/`,p)}
// 获取家庭详情
export const APIfamilydetail = id => { return axios.get(`${host}family/${id}/`)}
// 修改家庭详情
export const APIfamilyupdate = (id,p) => { return axios.patch(`${host}family/${id}/`,p)}


